body {
  background: #dcdcdc;
}

h2 {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 200;
  font-size: 44px;
  line-height: 0.90909;
  letter-spacing: -0.06em;
  color: #323232;
}

.btn__blc {
  width: 100%;
  border-radius: 5px;
  background: #000;
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.55;
  letter-spacing: -0.04em;
  text-align: center;
  color: #fff;
  padding: 8px 6px 6px;
  border: none;
}

.container {
  margin: 0 auto;
  max-width: 1110px;
  display: block;
  padding: 0 30px;
  box-sizing: content-box;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.7);
  z-index: 5;
  padding: 10px 0;
  backdrop-filter: blur(10px);
}
.nav ul {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 28px;
}
.nav ul a {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 10px;
  line-height: 1.2;
  letter-spacing: -0.05em;
  text-transform: uppercase;
  color: #7a7a7a;
  transition: all 0.3s;
  position: relative;
}
.nav ul a::before {
  content: "";
  display: block;
  width: 0;
  right: 0;
  left: auto;
  position: absolute;
  bottom: 0;
  height: 1px;
  background: #7a7a7a;
  transition: all 0.3s;
}
.nav ul a:hover {
  color: #323232;
}
.nav ul a:hover::before {
  right: auto;
  left: 0;
  width: 100%;
  background: #323232;
}
.nav ul li:last-child {
  margin: 0 0 0 auto;
}
.nav__hum, .nav__close {
  display: none;
}

.header {
  position: relative;
}
.header .container {
  position: relative;
  width: 100%;
}
.header__slide {
  min-height: 100vh;
  position: relative;
  z-index: 2;
  display: flex;
  padding: 0 0 30px;
}
.header__slide .container {
  margin: auto auto 0;
}
.header__slide img {
  position: absolute;
  width: 100%;
  height: 100%;
  display: block;
  pointer-events: none;
  object-fit: cover;
  z-index: -1;
}
.header__slide p {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 1.09017;
  letter-spacing: -0.02em;
  color: #fff;
}
.header__slide p + p {
  margin: 30px 0 0;
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1;
  letter-spacing: -0.03em;
  color: #fff;
}
.header__logo {
  position: absolute;
  top: 41px;
  left: 15px;
  max-width: 78px;
  z-index: 2;
}
.header__logo img {
  width: 100%;
}

.about {
  padding: 100px 0 0;
}
.about .container {
  display: grid;
  grid-template-columns: 90px 1fr;
  align-items: start;
  gap: 15px;
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14286;
  letter-spacing: -0.04em;
  color: #323232;
  position: relative;
}
.about .container img {
  width: 100%;
  object-fit: cover;
}
.about .container img + p {
  margin: 18px 0 0;
}
.about .container p,
.about .container li {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14286;
  letter-spacing: -0.04em;
  color: #323232;
}
.about .container ul li,
.about .container ol li {
  padding: 0 0 0 21px;
  position: relative;
}
.about .container ul li::before,
.about .container ol li::before {
  content: "";
  display: block;
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  top: 7px;
  left: 8px;
  background: #323232;
}
.about .container h2 {
  margin: 12px 0 0;
}
.about__title + img {
  height: 100%;
}
.about__container {
  display: grid;
  grid-template-columns: 437fr 657fr;
  grid-column: 2/3;
  gap: 15px;
  position: relative;
}
.about__container img {
  width: 100%;
}
.about__col {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  gap: 35px;
}
.about__fix {
  width: 90px;
}
.about__fix img {
  height: 90px;
}
.about__fix.fix {
  position: fixed;
  top: 50px;
}
.about__fix.bottom {
  position: absolute;
  left: 0;
  bottom: 0;
}

.for {
  padding: 32px 0 60px;
}
.for .container {
  display: grid;
  grid-template-columns: 90px 1fr;
  align-items: start;
  gap: 15px;
  position: relative;
}
.for__fix {
  width: 90px;
}
.for__fix.fix {
  position: fixed;
  top: 50px;
}
.for__fix.bottom {
  position: absolute;
  left: 0;
  bottom: 0;
}
.for__wrapper {
  display: grid;
  grid-template-columns: 264px 1fr;
  gap: 27px;
  align-items: start;
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.14286;
  letter-spacing: -0.04em;
  color: #323232;
  grid-column: 2/3;
}
.for__wrapper img {
  width: 100%;
  height: 264px;
  object-fit: cover;
}
.for__wrapper p,
.for__wrapper li {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.14286;
  letter-spacing: -0.04em;
  color: #323232;
}
.for__wrapper ul li,
.for__wrapper ol li {
  padding: 0 0 0 21px;
  position: relative;
}
.for__wrapper ul li::before,
.for__wrapper ol li::before {
  content: "";
  display: block;
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  top: 7px;
  left: 8px;
  background: #323232;
}

.master {
  position: relative;
  overflow: hidden;
  z-index: 2;
  padding: 173px 0 184px;
}
.master__dop {
  display: none;
}
.master__row {
  display: flex;
  justify-content: end;
  max-width: 760px;
  margin: 0 auto;
  gap: 20px;
  flex-wrap: wrap;
}
.master__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  pointer-events: none;
  left: 0;
  top: 0;
  object-position: left;
}
.master__wrapper {
  width: 100%;
  max-width: 370px;
  position: relative;
  z-index: 3;
  padding: 30px 30px 32px;
}
.master__wrapper::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 30px;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  z-index: -1;
}
.master__wrapper::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 32px;
  background: #fff;
  mask: url("../img/mask.svg");
  bottom: 0;
  left: 0;
  z-index: -1;
}
.master__wrapper h3 {
  margin: 37px 0 0;
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 28px;
  line-height: 1;
  letter-spacing: -0.02em;
  color: #323232;
}
.master__wrapper h3 + p {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14286;
  letter-spacing: -0.04em;
  color: #323232;
  margin: 5px 0 0;
}
.master__wrapper .btn__blc {
  margin: 25px 0 0;
}
.master__title {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
.master__title_date {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.55;
  letter-spacing: -0.04em;
  text-align: center;
  color: #fff;
  padding: 10px 21px 8px;
  border-radius: 5px;
  background: #000;
}
.master__title_type {
  border-radius: 4px;
  padding: 8px 21px 8px;
  background: #d9d9d9;
  opacity: 0.4;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
  letter-spacing: -0.04em;
  text-align: center;
  color: #000;
}
.master__title_type p {
  text-box-trim: trim-end;
  line-height: 1.5;
}
.master__info {
  margin: 10px 0 0;
  padding: 12px 0 0;
  border-top: dashed 1px #d9d9d9;
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14286;
  letter-spacing: -0.04em;
  color: #323232;
}
.master__info p,
.master__info li,
.master__info a {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14286;
  letter-spacing: -0.04em;
  color: #323232;
}
.master__info strong {
  font-weight: 700;
}
.master__info ul li,
.master__info ol li {
  padding: 0 0 0 21px;
  position: relative;
}
.master__info ul li::before,
.master__info ol li::before {
  content: "";
  display: block;
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  top: 7px;
  left: 8px;
  background: #323232;
}
.master__info a {
  text-decoration: underline;
  text-decoration-skip-ink: none;
}

.course {
  position: relative;
  padding: 107px 0;
}
.course__bg {
  position: absolute;
  top: 130px;
  right: 60px;
  width: 447px;
}
.course__row {
  display: flex;
  justify-content: start;
  max-width: 760px;
  margin: 0 auto;
  gap: 20px;
  flex-wrap: wrap;
}

.footer {
  padding: 20px 0;
  background: #dcdcdc;
}
.footer__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  gap: 26px;
}
.footer__col p {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36364;
  letter-spacing: -0.05em;
  color: #020202;
}
.footer__col ul {
  margin: 25px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 15px;
}
.footer__col ul a {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 11px;
  line-height: 1.45455;
  letter-spacing: -0.05em;
  color: #020202;
  transition: all 0.3s;
  position: relative;
}
.footer__col ul a::before {
  content: "";
  display: block;
  width: 0;
  right: 0;
  left: auto;
  position: absolute;
  bottom: 0;
  height: 1px;
  background: #020202;
  transition: all 0.3s;
}
.footer__col ul a:hover::before {
  right: auto;
  left: 0;
  width: 100%;
}
.footer__ask p {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36364;
  letter-spacing: -0.05em;
  text-transform: uppercase;
  color: #020202;
}
.footer__ask div {
  display: flex;
}
.footer__ask img {
  width: 72px;
  height: 72px;
  border-radius: 100%;
}
.footer__ask a {
  display: block;
  margin: 0 0 0 -18px;
}
.footer__sub {
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: end;
}
.footer__autor img {
  width: 78px;
}
.footer__autor p {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 9px;
  line-height: 1.11111;
  letter-spacing: -0.06em;
  color: #000;
}
.footer__row {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 20px;
}
.footer__row a,
.footer__row p {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 9px;
  line-height: 1.11111;
  letter-spacing: -0.06em;
  color: #000;
}
.footer__row a {
  transition: all 0.3s;
  position: relative;
}
.footer__row a::before {
  content: "";
  display: block;
  width: 0;
  right: 0;
  left: auto;
  position: absolute;
  bottom: 0;
  height: 1px;
  background: #020202;
  transition: all 0.3s;
}
.footer__row a:hover::before {
  right: auto;
  left: 0;
  width: 100%;
}

.modal {
  display: flex;
  padding: 30px 15px;
  overflow-y: auto;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.3s;
  pointer-events: none;
  z-index: 100;
}
.modal.active {
  opacity: 1;
  pointer-events: all;
}
.modal__overflow {
  position: fixed;
  inset: 0;
  z-index: -1;
  background: rgba(0, 0, 0, 0.5);
}
.modal__close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 12px;
  height: 12px;
  cursor: pointer;
}
.modal__close svg {
  width: 12px;
  height: 12px;
}
.modal__block {
  margin: auto;
  padding: 30px;
  border-radius: 5px;
  background: #fff;
  width: 100%;
  max-width: 500px;
  position: relative;
}
.modal__block h2 {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 28px;
  line-height: 1;
  letter-spacing: -0.02em;
  color: #323232;
  text-align: center;
}
.modal__block p {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14286;
  letter-spacing: -0.04em;
  color: #323232;
  margin: 10px 0 30px;
  text-align: center;
}
.modal__block input {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.55;
  letter-spacing: -0.04em;
  color: #000;
  padding: 10px 20px 8px;
  border-radius: 5px;
  background: rgba(217, 217, 217, 0.4);
  width: 100%;
  box-sizing: border-box;
  margin: 5px 0 0;
  border: none;
}
.modal__block input::placeholder {
  color: rgba(0, 0, 0, 0.4);
}
.modal__block .btn__blc {
  margin: 15px 0 0;
}

.modal-master__info {
  margin: 30px 0 0;
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14286;
  letter-spacing: -0.04em;
  color: #323232;
}

@media (max-width: 1024px) {
  .course .container {
    display: grid;
    grid-template-columns: 366px 1fr;
    align-items: center;
    gap: 30px;
  }
  .course__bg {
    position: relative;
    inset: auto;
    width: 100%;
    order: 2;
  }
}
@media (max-width: 768px) {
  .about__container {
    grid-template-columns: 1fr;
  }
  .footer__wrapper {
    grid-template-columns: 1fr 1fr;
  }
  .footer__ask {
    grid-column: 1/3;
    margin: 0;
  }
  .footer__sub {
    flex-direction: column;
    gap: 30px;
    align-items: start;
    margin: 30px 0 0;
  }
  .footer__autor {
    order: 2;
  }
  .footer__row {
    flex-direction: column;
    gap: 10px;
    align-items: start;
  }
  .course .container {
    grid-template-columns: 1fr;
  }
  .course__bg {
    margin: 0 auto;
    display: block;
    max-width: 350px;
  }
  .course .master__wrapper {
    margin: 0 auto;
  }
  .master__wrapper {
    margin: 0 auto;
  }
}
@media (max-width: 600px) {
  .container {
    padding: 0 15px;
  }
  .nav {
    backdrop-filter: none;
    background: transparent;
  }
  .nav::before {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    backdrop-filter: blur(10px);
    background: rgba(255, 255, 255, 0.7);
    z-index: -2;
  }
  .nav__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    background: #000;
    background: rgba(255, 255, 255, 0.7);
    z-index: 5;
    padding: 10px;
    backdrop-filter: blur(10px);
    padding: 150px 15px 50px;
    min-width: 200px;
    width: 75%;
    transition: all 0.3s;
    transform: translateX(-105%);
  }
  .nav__wrapper ul {
    display: grid;
    grid-template-columns: 1fr;
  }
  .nav__wrapper ul a {
    font-size: 16px;
  }
  .nav__wrapper ul li {
    margin: 0 !important;
  }
  .nav__close {
    position: absolute;
    left: 15px;
    top: 30px;
    font-family: "Helvetica Neue", sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.2;
    letter-spacing: -0.05em;
    text-transform: uppercase;
    color: #7a7a7a;
    display: block;
    cursor: pointer;
  }
  .nav__hum {
    font-family: "Helvetica Neue", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.2;
    letter-spacing: -0.05em;
    text-transform: uppercase;
    color: #7a7a7a;
    display: block;
    cursor: pointer;
  }
  .nav.active .nav__wrapper {
    transform: none;
  }
  .header__slide p {
    font-size: 24px;
  }
  .about__title {
    grid-template-columns: 1fr;
  }
  .about__title p {
    padding: 0;
  }
  .about .container {
    grid-template-columns: 70px 1fr;
    gap: 15px;
  }
  .about .container p {
    font-size: 12px;
  }
  .about__col {
    gap: 20px;
  }
  .about h2 {
    font-size: 30px;
  }
  .about__fix {
    width: 70px;
  }
  .about__fix img {
    height: 70px;
  }
  .about__fix.bottom {
    left: 15px !important;
  }
  .for .container {
    grid-template-columns: 70px 1fr;
    gap: 15px;
  }
  .for__wrapper {
    grid-template-columns: 1fr;
  }
  .for h2 {
    font-size: 30px;
  }
  .for__fix {
    width: 70px;
  }
  .for__fix.bottom {
    left: 15px !important;
  }
  .master__wrapper {
    margin: 0;
    padding: 30px 20px;
  }
  .course .master__wrapper {
    margin: 0;
  }
  .modal__block {
    padding: 30px 15px;
  }
}
@media (max-width: 320px) {
  .footer__wrapper {
    grid-template-columns: 1fr;
  }
  .footer__ask {
    grid-column: auto;
  }
  .footer__col ul {
    margin: 15px 0 0;
    gap: 10px;
  }
}